
// ── 根容器（CSS 变量定义在此，scoped 内有效） ─────────────────────────────
.creator-root {
  --green: #00ff41;
  --green-dim: #00cc33;
  --green-glow: rgba(0, 255, 65, 0.35);
  --bg: #000000;
  --bg-panel: #080808;
  --bg-field: #0a0a0a;
  --border: rgba(0, 255, 65, 0.4);
  --border-hover: rgba(0, 255, 65, 0.8);
  --font: 'Microsoft YaHei', '微软雅黑', sans-serif;
  --radius: 4px;

  width: 100%;
  background: var(--bg);
  color: var(--green);
  font-family: var(--font);
  font-size: 13px;
  line-height: 1.6;
  padding: 16px;
  box-sizing: border-box;
}

// ── 已提交摘要 ─────────────────────────────────────────────────────────────
.summary-view {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 12px;
}

.summary-header {
  display: flex;
  align-items: center;
  gap: 10px;

  h2 {
    font-family: var(--font);
    font-weight: 700;
    font-size: 16px;
    color: var(--green);
    margin: 0;
    text-shadow: 0 0 8px var(--green-glow);
  }

  .glitch-icon {
    color: var(--green-dim);
    font-size: 14px;
  }
}

.summary-grid {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 6px;
  border: 1px solid var(--border);
  border-radius: var(--radius);
  padding: 10px 12px;
  background: var(--bg-panel);
}

.summary-item {
  display: flex;
  gap: 12px;
  font-size: 12px;

  .summary-label {
    font-weight: 700;
    color: var(--green);
    min-width: 56px;
    flex-shrink: 0;
  }

  .summary-value {
    color: rgba(0, 255, 65, 0.75);
    word-break: break-word;
  }
}

.summary-hint {
  font-size: 11px;
  color: rgba(0, 255, 65, 0.45);
  margin: 0;
  letter-spacing: 0.04em;
}

// ── 表单视图 ──────────────────────────────────────────────────────────────
.form-view {
  display: flex;
  flex-direction: column;
  gap: 14px;
}

.form-header {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 4px;

  .header-deco {
    font-size: 10px;
    color: var(--green-dim);
    letter-spacing: 6px;
    opacity: 0.6;
  }
}

.title {
  font-family: var(--font);
  font-weight: 700;
  font-size: 20px;
  color: var(--green);
  margin: 4px 0;
  text-shadow: 0 0 12px var(--green-glow), 0 0 24px rgba(0, 255, 65, 0.15);
  letter-spacing: 0.12em;
}

.subtitle {
  font-size: 11px;
  color: rgba(0, 255, 65, 0.5);
  margin: 0;
  letter-spacing: 0.04em;
}

// ── 表单主体 ──────────────────────────────────────────────────────────────
.form-body {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.form-row {
  display: flex;
  gap: 10px;
}

// ── 字段 ──────────────────────────────────────────────────────────────────
.field {
  display: flex;
  flex-direction: column;
  gap: 4px;
  flex: 1;

  &--sm {
    flex: 0 0 110px;
  }

  &--full {
    width: 100%;
  }
}

.field-label {
  font-family: var(--font);
  font-weight: 700;
  font-size: 11px;
  color: var(--green);
  text-transform: uppercase;
  letter-spacing: 0.08em;

  &--extra {
    font-weight: 400;
    font-size: 10px;
    color: rgba(0, 255, 65, 0.45);
    text-transform: none;
    letter-spacing: 0;
  }
}

%input-base {
  width: 100%;
  background: var(--bg-field);
  border: 1px solid var(--border);
  border-radius: var(--radius);
  color: #ccffcc;
  font-family: 'Consolas', 'Courier New', monospace;
  font-size: 12px;
  padding: 6px 8px;
  box-sizing: border-box;
  outline: none;
  transition: border-color 0.15s, box-shadow 0.15s;
  resize: none;

  &::placeholder {
    color: rgba(0, 255, 65, 0.2);
  }

  &:focus {
    border-color: var(--border-hover);
    box-shadow: 0 0 6px var(--green-glow);
  }

  &:disabled {
    opacity: 0.45;
    cursor: not-allowed;
  }
}

.field-input {
  @extend %input-base;
}

.field-textarea {
  @extend %input-base;
  line-height: 1.5;
}

// ── 性别选择 ──────────────────────────────────────────────────────────────
.gender-group {
  display: flex;
  gap: 5px;
}

.gender-btn {
  flex: 1;
  padding: 5px 4px;
  background: transparent;
  border: 1px solid var(--border);
  border-radius: var(--radius);
  color: rgba(0, 255, 65, 0.55);
  font-family: var(--font);
  font-weight: 700;
  font-size: 11px;
  cursor: pointer;
  transition: all 0.15s;

  &:hover:not(:disabled) {
    border-color: var(--green);
    color: var(--green);
    background: rgba(0, 255, 65, 0.07);
  }

  &--active {
    border-color: var(--green) !important;
    background: rgba(0, 255, 65, 0.15) !important;
    color: var(--green) !important;
    box-shadow: 0 0 6px var(--green-glow);
  }

  &:disabled {
    opacity: 0.4;
    cursor: not-allowed;
  }
}

// ── 人设库 ────────────────────────────────────────────────────────────────
.preset-section {
  display: flex;
  flex-direction: column;
  gap: 6px;
  border: 1px solid var(--border);
  border-radius: var(--radius);
  padding: 8px 10px;
  background: var(--bg-panel);
}

.preset-row {
  display: flex;
  gap: 6px;
  align-items: center;
}

.preset-select {
  flex: 1;
  background: var(--bg-field);
  border: 1px solid var(--border);
  border-radius: var(--radius);
  color: #ccffcc;
  font-family: 'Consolas', 'Courier New', monospace;
  font-size: 12px;
  padding: 5px 8px;
  outline: none;
  cursor: pointer;
  transition: border-color 0.15s;

  &:focus {
    border-color: var(--border-hover);
    box-shadow: 0 0 6px var(--green-glow);
  }

  option {
    background: #0a0a0a;
    color: #ccffcc;
  }

  &:disabled {
    opacity: 0.45;
    cursor: not-allowed;
  }
}

.preset-btn {
  flex-shrink: 0;
  padding: 5px 10px;
  background: transparent;
  border: 1px solid var(--border);
  border-radius: var(--radius);
  color: rgba(0, 255, 65, 0.7);
  font-family: var(--font);
  font-weight: 700;
  font-size: 11px;
  cursor: pointer;
  transition: all 0.15s;
  white-space: nowrap;

  &:hover:not(:disabled) {
    border-color: var(--green);
    color: var(--green);
    background: rgba(0, 255, 65, 0.07);
  }

  &:disabled {
    opacity: 0.35;
    cursor: not-allowed;
  }

  &--save {
    border-color: rgba(0, 255, 65, 0.6);
    color: var(--green);
  }

  &--save:hover:not(:disabled) {
    background: rgba(0, 255, 65, 0.12);
    box-shadow: 0 0 6px var(--green-glow);
  }

  &--danger {
    border-color: rgba(255, 60, 60, 0.4);
    color: rgba(255, 100, 100, 0.7);
  }

  &--danger:hover:not(:disabled) {
    border-color: rgba(255, 60, 60, 0.8);
    color: #ff6464;
    background: rgba(255, 60, 60, 0.08);
  }
}

// ── 提交按钮 ──────────────────────────────────────────────────────────────
.form-footer {
  display: flex;
  justify-content: center;
  padding-top: 4px;
}

.submit-btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  min-width: 160px;
  padding: 10px 28px;
  background: var(--green);
  border: none;
  border-radius: var(--radius);
  color: #000000;
  font-family: var(--font);
  font-weight: 700;
  font-size: 14px;
  letter-spacing: 0.1em;
  cursor: pointer;
  transition: all 0.2s;
  box-shadow: 0 0 14px var(--green-glow);

  &:hover:not(:disabled) {
    background: #33ff66;
    box-shadow: 0 0 22px rgba(0, 255, 65, 0.6);
    transform: translateY(-1px);
  }

  &:active:not(:disabled) {
    transform: translateY(0);
  }

  &:disabled {
    opacity: 0.55;
    cursor: not-allowed;
    box-shadow: none;
  }
}

.btn-inner {
  display: flex;
  align-items: center;
  gap: 6px;
}

// ── 旋转动画 ──────────────────────────────────────────────────────────────
.spinner {
  display: inline-block;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  from { transform: rotate(0deg); }
  to   { transform: rotate(360deg); }
}
